<template>
  <mainLayout>
    <template #header v-if="includeHeader"></template>
    <template #default>
      <div class="main-content">
        <slot></slot>
      </div>
    </template>
    <template #footer v-if="includeFooter"></template>
  </mainLayout>
</template>

<script>
import mainLayout from "@/components/app/layoutElements/MainLayout.vue";

export default {
  name: "BaseView",
  components: {
    mainLayout,
  },
  beforeMount() {
    if (localStorage.getItem("path").length > 0) {
      this.$router.options.routes.forEach((route) => {
        if (route.path === localStorage.getItem("path")) {
          this.$router.push(localStorage.getItem("path"));
          localStorage.removeItem("path");
          return;
        } else {
          this.$router.push("404");
        }
      });
    }
  },
  props: {
    includeHeader: {
      type: Boolean,
      default: true,
    },
    includeFooter: {
      type: Boolean,
      default: true,
    },
  },
};
</script>

<style scoped>
h1 {
  margin-bottom: 1rem;
}
</style>
